:root {
  --primary: #fbcfe8;
  --secondary: #d8b4fe;
  --text-dark: #374151;
  --bg: #fff0f6;
  --card-bg: #ffffff;
  --card-border: #e5e7eb;
  --link-color: #9333ea;
  --link-hover: #7c3aed;
}

body.dark {
  --primary: #7c3aed;
  --secondary: #6d28d9;
  --text-dark: #e5e7eb;
  --bg: #111827;
  --card-bg: #1f2937;
  --card-border: #374151;
  --link-color: #c084fc;
  --link-hover: #e9d5ff;
  background-color: var(--bg);
  color: var(--text-dark);
}

html, body, #root {
  margin: 0;
  padding: 0;
  background-color: var(--bg);
  color: var(--text-dark);
  font-family: 'Poppins', sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
  scroll-behavior: smooth;
}

.App {
  max-width: 900px;
  margin: auto;
  padding: 2rem;
  box-sizing: border-box;
}

/* Navigation Bar */
.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--bg);
  border-bottom: 1px solid var(--card-border);
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  z-index: 1000;
  backdrop-filter: blur(10px);
}

.nav a {
  text-decoration: none;
  color: var(--link-color);
  font-weight: 600;
  font-size: 1rem;
  transition: color 0.2s ease;
}

.nav a:hover {
  color: var(--link-hover);
}

/* Hero Section */
.hero {
  background: linear-gradient(to right, var(--primary), var(--secondary));
  padding: 4rem 2rem 3rem;
  border-radius: 1.5rem;
  text-align: center;
  margin-top: 6rem;
}

.hero h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

/* Section Styling */
section {
  padding: 4rem 2rem;
  min-height: 80vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Projects */
.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

.project-card {
  width: 250px;
  min-height: 180px;
  padding: 1rem;
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 1rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.08);
}

/* Contact Links */
.contact a {
  color: var(--link-color);
  text-decoration: none;
  font-weight: 500;
}

.contact a:hover {
  text-decoration: underline;
  color: var(--link-hover);
}

/* Message Box */
.qa-box {
  background: var(--card-bg);
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
  margin-top: 4rem;
  transition: background-color 0.3s ease;
}

/* Tooltips */
.tooltip {
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.tooltiptext {
  visibility: hidden;
  background-color: #fdf6f0;
  color: #333;
  text-align: center;
  border-radius: 8px;
  padding: 0.5rem;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  white-space: nowrap;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

/* Dark Mode Overrides */
.dark-mode {
  background-color: var(--bg);
  color: var(--text-dark);
}

.dark-mode .project-card {
  background-color: var(--card-bg);
  color: var(--text-dark);
}

.dark-mode a {
  color: var(--link-color);
}

.dark-mode blockquote {
  color: #e0e7ff;
}

/* Responsive Tweaks */
@media (max-width: 600px) {
  .nav {
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1rem;
  }

  .hero h1 {
    font-size: 2rem;
  }

  .project-card {
    width: 100%;
    max-width: 300px;
  }
}
